.profileForm {
  display: grid;
  grid-template-columns: 25% 40% 1fr;
  justify-items: center;
  &__btn {
    justify-self: center;
    font-size: 15px;
    padding: 0 20px;
    line-height: 30px;
  }
  &__item {
    grid-column: 2;
    input{
      border: 1px solid black;
      width: 200px;
      padding: 2px 10px;
    }
    textarea {
      padding: 2px 10px;
      border: 1px solid black;
      align-self: center;
      margin: 7px
    }
  }
  .error{
    grid-column: span 2;
    &__message{
      font-size: 18px;
      color: red;
      outline: red solid 1px;
    }
  }
}

label {
  grid-column: 1;
  align-self: center;
  justify-self: center;
  cursor: pointer;
}
